body{
    background-color: lightgrey;
}

.home{
    color: red;
    font-size: 30px;

    &:hover{
        transition: 300ms;
        color: blueviolet;
    }
}

.form_container{
   width: -webkit-fill-available;
   padding: 10% 15%;
}

.form_card{
    background-color: white;
    width: 100%;
    padding: 20px;
}

.input_username{
    width: -webkit-fill-available;
    height: 40px;
    border: 1px solid darkgrey;
    padding: 5px;
    border-radius: unset;
    margin-bottom: 10px;
}

.btn_search{
    width: 100%;
    height: 40px;
    background-color: rgb(30, 167, 253);
    color: white;
    border: unset;
    font-size: 15px;

    &:hover{
        transition: 300ms;
        background-color: black;
        cursor: pointer;
    }
}

.div_list_user{
    margin-top: 20px;
}

.div_info{
    margin-top: 15px;
    margin-bottom: 15px;
}

.div_repo{
    background-color: lightgrey;
    padding: 10px;
    margin-bottom: 10px;
}

.div_repo_name{
    font-size: 16px;
    font-weight: 700;
}

.accordion_header > button{
    color: black !important;
}

.div_repo_desc{
    display: inline-block;
    word-break: break-word;
}